.sidebar {
    width: 25vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    padding: 10px;
    z-index: 1000;
    background-color: white;
    transform: translateX(-100%);
    transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
}

.sidebar.open {
    transform: translateX(0);
}

.background {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 100;
    background-color: rgba(0, 0, 0, 0.4);
    backdrop-filter: blur(3px);
}

.nav {
    display: flex;
    flex-direction: column;
    gap: 45px;
    padding: 30px;
}

.nav a {
    font-size: 2.2rem;
    color: black;
    text-decoration: none;
    transition: all 0.2s;
}

.nav a:hover {
    color: #58ACFF;
}

.nav a:active {
    color: #3D6B99;
}

.nav a.active {
    color: #0059B2;
    text-decoration: underline;
}

.close {
    align-self: flex-end;
    cursor: pointer;
    border: none;
    background-color: transparent;
}

.close:hover img {
    opacity: 0.7;
}

@media only screen and (max-width: 62.5em) {
    .sidebar {
        width: 40vw;
    }
}

@media only screen and (max-width: 50em) {
    .sidebar {
        width: 50vw;
    }
}

@media only screen and (max-width: 37.5em) {
    .sidebar {
        width: 100vw;
    }

    .nav a {
        font-size: 3rem;
    }
}
